* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #eeeeee;
}

.produtos {
  padding: 0px;
  margin: 0 auto;
  width: 90%;
  max-width: 600px;
  margin-bottom: 20px;
  justify-content: center;
  align-items: center;
  border: solid #eeeeeefa;
  border-radius: 5px;
  background-color: #ffffff;
  box-shadow: 1px 3px 5px #c2c0c0fa;
}

.produto {
  margin: 1 auto;
  font-size: 13px;
  display: flex;
  width: 100%;
  padding: 0px;
  justify-content: center;
  background-color: #ffffff;
  display: inline-flex;
}

.title {
  padding-top: 15px;
  padding-left: 40px;
  margin: 0 auto;
  justify-content: center;
  font-weight: bolder;
  color: #999999;
}

.button {
  padding: 20px;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 15px;
  font-weight: bolder;
  border-style: outset;
  font-size: 20px;
  display: flex;
  background-color: #ff6c00;
  color: #fff6ef;
  width: 90%;
  max-width: 600px;
  justify-content: center;
  border: none;
  border-radius: 3px;
  box-shadow: 0.5px 0.5px 10px #999898;
}

.total {
  margin-bottom: 20px;
  margin: 0 auto;
  width: 90%;
  max-width: 600px;
}

.header {
  background-color: #ffffff;
  font-size: 15px;
  font-weight: bold;
  color: #cccccc;
  display: flex;
  justify-content: space-around;
  list-style: none;
  padding: 12px;
  box-shadow: 0.5px 0.5px 15px #999898;
}

.cart {
  color: #ff7800;
}
