.div {
  display: flex;
  padding: 10px;
  width: 85%;
  margin: 10px;
  background-color: #ffffff;
  border: solid #d6d5d5fa;
  border-radius: 3px;
  border-width: 1px;
}

.cart {
  display: flex;
  flex-direction: column;
}

.price {
  align-self: end;
  font-weight: bolder;
}

img {
  padding: 5px;
}
